/* eslint-disable no-new */
import Swiper, { Autoplay, Navigation, Pagination } from "swiper";

const navSlider = new Swiper(".js-navSlider", {
	modules: [Navigation],
	loop: false,
	slidesPerView: "auto",
	spaceBetween: 0,
	navigation: {
		nextEl: ".header__button--next",
		prevEl: ".header__button--prev",
		disabledClass: "small-navigation--disabled",
	},
});

const heroSlider = new Swiper(".hero__slider .swiper", {
	modules: [Navigation, Pagination, Autoplay],
	loop: true,
	// autoplay: {
	// 	delay: 3000,
	// 	pauseOnMouseEnter: true,
	// },
	slidesPerView: 1,
	spaceBetween: 20,
	navigation: {
		nextEl: ".hero__navigation--next",
		prevEl: ".hero__navigation--prev",
		disabledClass: "navigation--disabled",
	},
	pagination: {
		el: ".hero-pagination",
		type: "bullets",
		clickable: "true",
	},
	on: {
		init: function (swiper) {
			swiper.pagination.render();
			swiper.pagination.update();
		},
		slideChange: function (swiper) {
			swiper.pagination.render();
			swiper.pagination.update();
		},
	},
});

heroSlider.on("slideChange", function () {
	const copy = document.querySelectorAll(".js-copy");
	copy.forEach((element) => {
		element.style.display = "";
		element.parentElement.querySelector(
			".advertising__link--complete"
		).style.display = "";
	});
});

