import "./vendor";
import "./helpers";
import "./components/slider";
import { ieFix } from "./vendor/ie-fix";
import { vhFix } from "./vendor/vh-fix";
import lazyLoading from "./modules/lazyLoading";
import { clearInput } from "./components/clearInput";
import { submenuInit } from "./components/submenu";
import { adversting } from "./components/ad";
import { tabInit } from "./components/tab";
import { accardion } from "./components/accardion";
import { inputFocus } from "./components/inputFocus";
import { scrollTop } from "./components/scrollUp";
import { addCartInit } from "./components/add-cart";
import { likeToggle } from "./components/likeToggle";
import { copyLink } from "./components/copy";

ieFix();
vhFix();
lazyLoading.init();
clearInput();
submenuInit();
adversting();
tabInit();
accardion();
inputFocus();
scrollTop();
addCartInit();
likeToggle();
copyLink();

const header = $(".header__top");
const headerFixed = $(".header__fixed, .scroll-up");
const headerHeight = header.innerHeight();
const body = $("body");

const heightHeader = () => {
	body.css("--hh", `${Math.round(headerHeight)}px`);
};

heightHeader();

const fixedHeader = () => {
	const scroll = $(window).scrollTop();
	if (scroll >= headerHeight) {
		headerFixed.addClass("is-active");
	} else {
		headerFixed.removeClass("is-active");
	}
};

fixedHeader();

const tooltipTriggerList = document.querySelectorAll(
	'[data-bs-toggle="tooltip"]'
);
const tooltipList = [...tooltipTriggerList].map((tooltipTriggerEl) => {
	let tooltip = new bootstrap.Tooltip(tooltipTriggerEl, {
		offset: [0, 10],
	});
});

$(window).on("resize", heightHeader);

$(window).scroll(fixedHeader);
